import ChatAI from '@/views/ChatAI.vue'
import ThreeD from '@/views/ThreeD.vue'
import BigData from '@/views/BigData.vue'
import * as THREE from 'three'
import HALO from 'vanta/src/vanta.halo'
import JinJiFenZha from './JinJiFenZha.vue'
import ZhuangTai from './ZhuangTai.vue'
import SheZhi from './SheZhi.vue'
import YiJianShunKong from './YiJianShunKong.vue'

export default {
  name: 'Hello',
  components: {
    ThreeD,
    BigData,
    ChatAI,
    BRow,
    BCol,
    BCard,
    DuoDianCeWen,
    YiJianShunKong,
    ZhuangTai,
    SheZhi,
    BTabs,
    BTab,
    BFormInput,
    BButton,
    JinJiFenZha,
  },
  data() {
    return {
      username: 'admin',
      adminpass: '',
      password: 'admin',
      isAdmin: false,
      activeTab: 0,
      tabs: [
        { title: '', content: '' },
        { title: 'Tab 1', content: '' },
        { title: 'Tab 2', content: '' },
        { title: 'Tab 3', content: '' },
        { title: 'Tab 4', content: '' },
        { title: 'Tab 5', content: '' },
        { title: 'Tab 6', content: '' },
        { title: 'Tab 7', content: 'Tab 8  ()' },
        { title: 'Tab 8', content: '' },
        { title: 'Tab 9', content: '3D' },
      ],
    }
  },
  computed: {
    ChuanCan() {
      this.adminpass = this.$store.getters.getChuanCan
      return this.$store.getters.getChuanCan
    },
  },
  mounted() {
    this.vantaEffect = HALO({
      el: this.$refs.vantaRef,
      THREE,
    })
  },
  beforeDestroy() {
    if (this.vantaEffect) {
      this.vantaEffect.destroy()
    }
  },
  methods: {
    login() {
      console.log('adminpass==', this.adminpass)
      // 
      if (this.username === 'admin' && this.password === this.adminpass) {
        console.log('here', this.adminpass)
        this.isAdmin = true
        this.$refs.loginModal.show()
      } else if (this.username === 'observer' && this.password === 'observer') {
        this.isAdmin = false
        // 
        this.$refs.loginModal.show()
      } else {
        console.log(this.password)
        alert('')
        // 
        // 
      }
    },
    logout() {
      this.isAdmin = false
    },
  },
}
</script>

