<template>
  <b-card style="background-color: #f2f4f6;">

    <b-card>
      <b-button
        style="font-size: 25px;font-family: ,serif;position: relative;width: 100%;text-align: left;color: #3a3a3f;display: flex; justify-content: space-between; align-items: center;"
        variant="outline-light"
        @click="toggle"
      >
        
        <span style="font-size: 26px;text-align: right;color: #a5a5b2;font-weight: bold;"> > </span>
      </b-button>
      <b-collapse
        id="collapse-2"
        v-model="isCollapsed"
      >
        <b-card style="background-color: #F5F5F5;">
          <b-input-group
            prepend=""
            class="one"
          >
            <b-form-input
              v-model="emailAddress"
              type="text"
              placeholder="807683237@qq.com"
            />
            <b-input-group-append>
              <b-button
                variant="outline-success"
                @click="storeEmailAddress"
              >
                
              </b-button>
              <b-button
                variant="info"
                @click="clearEmail"
              >
                
              </b-button>
            </b-input-group-append>
          </b-input-group>

          <b-modal
            ref="notificationModal"
            title="!"
            hide-footer
          >
            {{ notificationMessage }}
          </b-modal>
        </b-card>
      </b-collapse>
    </b-card>

    <b-card>
      <b-button
          style="font-size: 25px;font-family: ,serif;position: relative;width: 100%;text-align: left;color: #3a3a3f;display: flex; justify-content: space-between; align-items: center;"
        variant="outline-light"
        @click="togglehistory"
      >
        ()
        <span style="font-size: 26px;color: #a5a5b2;font-weight: bold"> > </span>
      </b-button>
      <b-collapse
        id="collapse-2"
        v-model="ishistory"
      >
        <b-card
          class="overflow-auto"
          style="background-color: #f2f2fa;margin-top: 50px"
        >
          <b-table
            id="data-table"
            class="table-item"
            :items="items"
            :per-page="perPage"
            :current-page="currentPage"
            :fields="tableFields"
            :bordered="bordered"
          />

          <b-pagination
            v-model="currentPage"
            :total-rows="rows"
            :per-page="perPage"
            aria-controls="my-table"
            class="page-item"
            first-number
            last-number
          />

          <p class="mt-3">
            : {{ currentPage }}
          </p>
        </b-card>
      </b-collapse>
    </b-card>

    <b-card>
      <b-button
          style="font-size: 25px;font-family: ,serif;position: relative;width: 100%;text-align: left;color: #3a3a3f;display: flex; justify-content: space-between; align-items: center;"
        variant="outline-light"
        @click="toggledianli"
      >
        
        <span style="font-size: 26px;color: #a5a5b2;font-weight: bold"> > </span>
      </b-button>
      <b-collapse
        id="collapse-2"
        v-model="isdianli"
      >
        <b-card style="background-color: #f5f4f4;margin-top: 50px">
          <div>

            <b-tabs card>
              <b-tab
                title=""
                active
              >
                <b-card class="overflow-auto" style="background-color: #57585b;overflow-x: scroll">
                  <b-card-text style="margin-bottom: 25px;color: #fdfcfc;flex-direction: row;align-items: center;"><span></span>
                    <span
                      class="data-value"
                      style="margin-left: 100px;"
                    >{{ voltage }}</span> <span style="margin-left: 10px">V</span>
                  </b-card-text>

                  <b-card-text style="margin-bottom: 25px;color: #fdfcfc;flex-direction: row;align-items: center;">
                    <span></span><span
                      class="data-value"
                      style="margin-left: 100px"
                    >{{ current }}</span> <span style="margin-left: 35px">A</span>
                  </b-card-text>

                  <b-card-text style="margin-bottom: 25px;color: #fdfcfc;flex-direction: row;align-items: center;">
                    <span></span><span
                      class="data-value"
                      style="margin-left: 100px"
                    >{{ apparentPower }}</span> <span style="margin-left: 5px">W</span>
                  </b-card-text>

                  <b-card-text style="margin-bottom: 25px;color: #fdfcfc;flex-direction: row;align-items: center;">
                    <span
                      class="data-value"
                      style="margin-left: 120px"
                    >{{ frequency }}</span><span style="margin-left: 10px">Hz</span>
                  </b-card-text>

                  <b-card-text style="margin-bottom: 25px;color: #fdfcfc;flex-direction: row;align-items: center;">
                    <span
                      class="data-value"
                      style="margin-left: 80px"
                    >{{ totalKWh }}</span><span style="margin-left:10px">KWH</span>
                  </b-card-text>

                  <b-card-text style="color: #fdfcfc;flex-direction: row;align-items: center;">
                    <span
                      class="data-value"
                      style="margin-left: 60px"
                    >{{ totalReactiveEnergy }}</span><span style="margin-left:10px">KvarH</span></b-card-text>
                  <div class="line-divider" />
                  <b-card-text style="margin-top: 18px;color: #faae76;"><span style="background-color: #858282;text-align: center;margin-left: 80px;padding: 8px 25px;">{{ currenttime }}</span>
                  </b-card-text>
                </b-card>
              </b-tab>

              <b-tab title="">
                <b-card class="overflow-auto" style="width: 600px;overflow-x: scroll">
                  <b-table
                    class="communication-text"
                    striped
                    hover
                    :items="communication"
                    :per-page="perPage"
                    :current-page="currentPage"
                  />
                  <b-pagination
                    v-model="currentPage"
                    :total-rows="corows"
                    :per-page="perPage"
                    aria-controls="my-table"
                    class="custom-warning"
                    first-number
                    last-number
                  />

                  <p class="mt-3">
                    : {{ currentPage }}
                  </p>
                </b-card>

              </b-tab>
            </b-tabs>

          </div>

        </b-card>
      </b-collapse>
    </b-card>

    <b-card>
      <b-button
          style="font-size: 25px;font-family: ,serif;position: relative;width: 100%;text-align: left;color: #3a3a3f;display: flex; justify-content: space-between; align-items: center;"
        variant="outline-light"
        @click="togglerecord"
      >
        
        <span style="font-size: 26px;color: #a5a5b2;font-weight: bold"> > </span>
      </b-button>
      <b-collapse
        id="collapse-2"
        v-model="isrecord"
      >
        <b-card
          class="overflow-auto"
          style="margin-top: 50px"
        >

          <b-card style="width: 695px;background-color: #f2f2fa;justify-content: center">
            <b-table
              class="record-text"
              striped
              hover
              :items="reitems"
              :fields="recordFields1"
              :per-page="perPage"
              :current-page="currentPage"
            />

            <b-table
              class="record-text"
              striped
              hover
              :items="reitems"
              :fields="recordFields2"
              :per-page="perPage"
              :current-page="currentPage"
            />
            <b-pagination
              v-model="currentPage"
              :total-rows="rerows"
              :per-page="perPage"
              aria-controls="my-table"
              class="custom-warning"
              first-number
              last-number
            />

            <p class="mt-3">
              : {{ currentPage }}
            </p>
          </b-card>

        </b-card>
      </b-collapse>
    </b-card>

  </b-card>

</template>

<script>

import {
  BButton,
  BCard,
  BCardText,
  BFormInput,
  BInputGroup,
  BInputGroupAppend,
  BInputGroupPrepend,
  BModal,
  BPagination,
  BTab,
  BTable,
  BTabs,
  BCollapse,
} from 'bootstrap-vue'
import axios from 'axios'

export default {
  components: {
    BPagination,
    BButton,
    BCard,
    BCardText,
    BInputGroup,
    // eslint-disable-next-line vue/no-unused-components
    BInputGroupPrepend,
    BFormInput,
    BInputGroupAppend,
    BModal,
    BTable,
    BTab,
    BTabs,
    BCollapse,
  },
  data() {
    return {
      // 
      voltage: 0,
      current: 0,
      apparentPower: 0,
      powerFactor: 0,
      frequency: 0,
      totalKWh: 0,
      totalReactiveEnergy: 0,
      currenttime: '',

      isrecord: false,
      isdianli: false,
      ishistory: false,
      isCollapsed: false,
      bordered: true, // table
      notificationMessage: '',
      perPage: 5, // 5
      currentPage: 1, // 1
      time: null, // 
      items: [], //  
      reitems: [], // 
      chatInstance: null,
      recordFields1: [
        { key: 'id', sortable: true, label: 'id' },
        { key: 'jiedifen', label: '' },
        { key: 'houmen', label: '' },
        { key: 'qianmen', label: '' },
        { key: 'duanluhe', label: '' },
        { key: 'currenttime', sortable: true, label: '' },
      ],
      recordFields2: [
        { key: 'id', sortable: true, label: 'id' },
        { key: 'jiedihe', label: '' },
        { key: 'gongzuowei', label: '' },
        { key: 'shiyanwei', label: '' },
        { key: 'duanlufen', label: '' },
        { key: 'currenttime', sortable: true, label: '' },
      ],
      tableFields: [
        { key: 'id', sortable: true, label: 'id' },
        { key: 'historysensor1', label: '1', formatter: value => `${value}C` },
        { key: 'historysensor2', label: '2', formatter: value => `${value}C` },
        { key: 'historysensor3', label: '3', formatter: value => `${value}C` },
        { key: 'historysensor4', label: '4', formatter: value => `${value}C` },
        { key: 'historysensor5', label: '5', formatter: value => `${value}C` },
        { key: 'historysensor6', label: '6', formatter: value => `${value}C` },
        { key: 'historytime', sortable: true, label: '' },
      ], // 
      communication: [],
    }
  },
  computed: {
    ChuanCan() {
      return this.$store.getters.getChuanCan
    },
    emailAddress: {
      get() {
        return this.$store.getters.getemailAddress
      },
      set(value) {
        this.$store.dispatch('setEmailAddress', value)
      },
    },
    rows() {
      return this.items.length
    },
    corows() {
      return this.communication.length
    },
    rerows() {
      return this.reitems.length
    },
  },
  mounted() {
    this.fetchrecord()
    this.startDataUpdates() // 

    // this.timer = setInterval(() => {
    //   this.updateData()
    // }, 5000) // 5

    console.log('Component mounted')
    // this.fetchTemperatures()
    //
    // this.fetchInterval = setInterval(() => {
    //   this.fetchTemperatures()
    // }, 5000) // 5

    setInterval(() => {
      this.updateCurrentTime()
    }, 1000) // 
  },
  beforeDestroy() {
    clearInterval(this.fetchInterval)
    clearInterval(this.timer)
  },
  methods: {
    startDataUpdates() {
      setInterval(() => {
        // 
        this.voltage = (Math.random() * 100 + 200).toFixed(2)//  
        this.current = (Math.random() * 5 + 5).toFixed(2) // 
        this.apparentPower = (Math.random() * 100 + 190).toFixed(2)
        this.powerFactor = (Math.random() * 80 + 200).toFixed(2)
        this.frequency = (Math.random() * 250 + 150).toFixed(2)
        this.totalKWh = (Math.random() * 70 + 200).toFixed(2)
        this.totalReactiveEnergy = (Math.random() * 90 + 210).toFixed(2)
      }, 2000) // 2
    },
    updateCurrentTime() {
      const now = new Date()
      // 
      this.currenttime = `${now.getFullYear()}-${this.padZero(now.getMonth() + 1)}-${this.padZero(now.getDate())} ${this.padZero(now.getHours())}:${this.padZero(now.getMinutes())}:${this.padZero(now.getSeconds())}` // 
    },
    padZero(value) {
      // 100
      return value < 10 ? `0${value}` : value
    },

    // updateData() { // http://localhost:8181/sample-data
    //   axios.get('http://localhost:10866/communication', { withCredentials: true })
    //     .then(response => {
    //       // 
    //       this.communication = response.data
    //       console.log('get the communication data')
    //       // this.renderChart()
    //     })
    //     .catch(error => {
    //       console.error('Error fetching data:', error)
    //     })
    // },
    fetchrecord() {
      axios.get('http://localhost:9999/records')
        .then(response => {
          console.log('get the recordData')
          this.reitems = response.data
          console.log('reitems:', this.reitems)
        })
        .catch(error => {
          console.error('Request failed:', error)
        })
    },
    // fetchTemperatures() {
    //   axios.get('http://localhost:10866/historytemperature')
    //     .then(response => {
    //       console.log('get the historytemperature')
    //       this.items = response.data
    //     })
    //     .catch(error => {
    //       console.error('Request failed:', error)
    //     })
    // },
    clearEmail() {
      this.emailAddress = '' // 
    },
    async storeEmailAddress() {
      // 
      await this.$store.dispatch('setEmailAddress', this.emailAddress)

      const { emailAddress } = this
      if (emailAddress) {
        try {
          // 
          const response = await axios.post('http://localhost:3000/saveemail', { email: emailAddress })
          if (response.status === 200) {
            // 
            this.notificationMessage = ''
          } else {
            console.error(':', response.data.message)
          }
        } catch (error) {
          this.notificationMessage = ''
          console.error(':', error)
        }
        this.$refs.notificationModal.show()
      } else {
        console.log('.')
        this.$refs.notificationModal.show()
      }
    },
    sendEmail() {
      const emailAddress = this.$store.getters.getemailAddress
      // eslint-disable-next-line no-empty
      if (emailAddress) {
      } else {
        console.log('Email address is not set.')
      }
    },
    toggle() {
      this.isCollapsed = !this.isCollapsed
    },
    togglehistory() {
      this.ishistory = !this.ishistory
    },
    toggledianli() {
      this.isdianli = !this.isdianli
    },
    togglerecord() {
      this.isrecord = !this.isrecord
    },
  },
}
</script>
<style scoped>
.page-item{
  justify-content: center;
}
.table-item{
  font-size: 14px;
  text-align: center;
}

.table-item th {
  text-align: center;
  font-size: 16px;
}

.introduction2 {
  font-size: 1px;
  font-family: ;
  color: #fdfdfd;

}

.card-text {
  font-size: 20px;
  font-family: ,serif;
  margin-left: 10px;
  color:#6A5ACD;
//background-color: lavender;
}

.communication-text{
  text-align: center;
  overflow: scroll;

}

.custom-warning {
  justify-content: center;
  border-color: #f0ad4e; /*  */
  color: #fff; /*  */
  margin-top: 20px;
  overflow: scroll;
}
.data-value {
  background-color: #9d9c9c;
  padding: 8px 30px;
//border: 1px solid #e8e7e7;
  margin-left: 75px;
  text-align: center;
}

.line-divider {
  margin-top: 25px; /*  */
  border-top: 2px solid #fdfcfc; /*  */
  width: 100%; /*  */
}
.record-text{
  text-align: center;
  width: 100%;
}

</style>
