<template>
  <div class="demo-inline-spacing">

    <b-card
      title=""
      style="width: 100vh;height: 100%"
    >
      <b-row style="margin-top: 50px;align-items: center;justify-content: center;margin-left: 50px">
        <b-col cols="12" sm="12" md="6">
          <b-button

            pill
            variant="outline-danger"
            class="xxxl"
            style="scale: 1.1"
            @click="confirmShutdown"
          >
            ,
          </b-button>
        </b-col>
        <b-col cols="12" sm="12" md="6">
          <b-button
            v-ripple.400="'rgba(0, 207, 232, 0.15)'"

            pill
            variant="outline-info"
            class="xxxl"
            style="scale: 1.1"
            @click="info"
          >
            ,

          </b-button>
        </b-col>
      </b-row>

      <b-modal
        ref="confirmationModal"
        hide-footer
        title=""
      >
        <b-button
          variant="danger"
          @click="performShutdown"
        >
          
        </b-button>
        <b-button
          variant="secondary"
          style="margin-left: 20px"
          @click="closeModal"
        >
          
        </b-button>
      </b-modal>

    </b-card>

  </div>
</template>

<script>
// import BCardCode from '@core/components/b-card-code/BCardCode.vue'
import {
  BCard, BButton, BCol, BRow,
} from 'bootstrap-vue'
import Ripple from 'vue-ripple-directive'
// import { codeTypes } from './code'
import Swal from 'sweetalert2'
import axios from 'axios'

export default {
  components: {
    // BCardCode,
    BCard,
    // BCardText,
    BButton,
    BCol,
    BRow,
  },
  directives: {
    Ripple,
  },
  data() {
    return {
      // codeTypes,
      userChoice: false, // 
    }
  },
  methods: {
    closeModal() {
      this.$refs.confirmationModal.hide()
    },
    confirmShutdown() {
      this.$refs.confirmationModal.show()
      // 
      this.userChoice = true
      this.closeModal()
    },

    performShutdown() {
      this.closeModal() // 
      this.showProcessingAlert()
      setTimeout(() => {
        this.showSuccessAlert()
      }, 2000) // 3
    },
    showProcessingAlert() {
      // eslint-disable-next-line no-unused-vars
      axios.post('http://10.168.1.103/fenzha').then(response => {
      })

      Swal.fire({
        title: '...',
        icon: 'warning',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
        timer: 2000, // 8
        timerProgressBar: true,
      })
    },
    showSuccessAlert() {
      Swal.fire({
        title: '!',
        text: '',
        icon: 'success',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },

    error() {
      Swal.fire({
        title: 'Error!',
        text: ' You clicked the button!',
        icon: 'error',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },

    // warning
    warning() {
      Swal.fire({
        title: 'Warning!',
        text: ' You clicked the button!',
        icon: 'warning',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },

    // info
    info() {
      Swal.fire({
        title: '!',
        text: '',
        icon: 'info',
        customClass: {
          confirmButton: 'btn btn-primary',
        },
        buttonsStyling: false,
      })
    },
  },
}
</script>
